import React, { Component } from "react";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import "./inviteHistory.scss";
import * as ScheduleActions from "../../store/Schedule/scheduleAction";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import WhatsappIcon from "../../assets/images/schedule/WhatsappIcon.svg";
import SmsIcon from "../../assets/images/schedule/SmsIcon.svg";
import GmailIcon from "../../assets/images/schedule/GmailIcon.svg";
import SelectInput from "../../components/Atoms/SelectInput/SelectInput";
import moment from "moment";
import ClickIcon from "../../assets/images/history/click.svg";
import CompleteIcon from "../../assets/images/history/complete.svg";
import DeliveryIcon from "../../assets/images/history/delivery.svg";
import FollowUp from "../../assets/images/history/followup.svg";
import LogIcon from "../../assets/images/history/log.svg";
import SentIcon from "../../assets/images/history/sent.svg";
import SubmitIcon from "../../assets/images/history/submit.svg";
import MessagePopup from "./MessagePopup";
import EmailPopup from "./EmailPopup";
import { Card, CardContent, IconButton, Chip } from "@material-ui/core";
import Navbar from "../../components/Navbar/Navbar";
import Tooltip, { tooltipClasses } from "@mui/material/Tooltip";
import { styled } from "@mui/material/styles";
import ReactGA from "react-ga";
import CloseCommentIcon from "../../assets/images/Icon ionic-ios-close-circle.svg";
import Modal from "@material-ui/core/Modal";
import Button from "../../components/Controls/Button/Button";
import SendIcon from "@mui/icons-material/Send";
import HistoryIcon from "@mui/icons-material/History";
const LightTooltip = styled(({ className, ...props }) => (
  <Tooltip {...props} classes={{ popper: className }} />
))(() => ({
  [`& .${tooltipClasses.tooltip}`]: {
    backgroundColor: "#fff",
    color: "#000000",
    fontSize: 12,
    fontWeight: "600",
  },
}));
class InviteHistory extends Component {
  state = {
    id: "",
    data: null,
    history: null,
    count: 0,
    scheduleCount: 0,
    options: [
      { label: "All", value: "All" },
      { label: "Sourced", value: "Sourced" },
      { label: "Invite", value: "Invite" },
      { label: "Interview", value: "Interview" },
      { label: "Conversation", value: "Conversation" },
      { label: "SMS", value: "SMS" },
      { label: "Whatsapp", value: "Whatsapp" },
      { label: "Mail", value: "Mail" },
      { label: "Debug", value: "Debug" },
      { label: "Error", value: "Error" },
    ],
    filter: { label: "All", value: "All" },
    timeline: {},
    scheduleId: "",
    email: [],
    phone: "",
    whatsappPopup: false,
    emailPopup: false,
    smsPopup: false,
    currentMessage: "",
    emailSubject: "",
    label: "",
    whatsappEvents: [],
    candidateId: "",
  };
  componentDidMount() {
    ReactGA.pageview(`${window.location.pathname}`);
    const data = this.props && this.props.location && this.props.location.state;
    ReactGA.pageview(window.location.pathname);
    let queryParams = new URLSearchParams(window.location.search);

    let id = queryParams.get("inviteId");
    let candidateId = queryParams.get("candidateId");
    console.log(candidateId);
    if (id && candidateId) {
      this.setState({ id: id, candidateId });
      this.props.ScheduleAction.inviteHistory({ inviteId: id, candidateId });
    } else if (candidateId) {
      this.setState({ candidateId });
      this.props.ScheduleAction.inviteHistory({ candidateId });
    }

    this.setState({ history: data });
    // localStorage.setItem("InviteHistoryData", data && data.name);
  }

  componentDidUpdate(prevProps, prevState) {
    const cur = this.props && this.props.scheduleReducer;
    const prev = prevProps && prevProps.scheduleReducer;

    if (cur.isMessagePosted !== prev.isMessagePosted && cur.isMessagePosted) {
      this.setState({
        currentMessage: "",
        // whatsappPopup: false,
        smsPopup: false,
      });

      this.state.whatsappPopup &&
        this.props.ScheduleAction.inviteHistory({
          inviteId: this.state.id,
          candidateId: this.state.candidateId,
        });
    }
    if (cur.isEmailPosted !== prev.isEmailPosted && cur.isEmailPosted) {
      this.setState({
        currentMessage: "",
        // emailPopup: false,
        emailSubject: "",
      });
    }
    if (
      prev.inviteHistory !== cur.inviteHistory &&
      cur.inviteHistory &&
      cur.inviteHistory.data &&
      cur.isInviteHistory
    ) {
      const invite = cur.inviteHistory.data;
      const { jdId, candidateId, scheduleId, jdCount, scheduleCount } = invite;
      this.setState(
        {
          count: jdCount,
          scheduleCount: scheduleCount,
          data: invite,
          scheduleId: scheduleId && scheduleId._id,
          email: [candidateId && candidateId.email],
          phone: candidateId && candidateId.phone,
          label:
            jdId &&
            candidateId &&
            `${jdId.title} ${jdId.minExp}
        ${jdId.minExp > 1 ? "years" : "year"} to
        ${jdId.maxExp}
        ${jdId.maxExp > 1 ? "years" : "year"} | ${candidateId.name}`,
        },
        this.filterEvent
      );
    }
  }

  filterEvent = () => {
    const { filter, data } = this.state;
    const eventData = data && data.events;
    const events = {};
    const whatsappevent = [];
    const filterVal = filter.label;
    eventData &&
      eventData.length > 0 &&
      eventData.map((el) => {
        const date =
          el &&
          el.timestamp &&
          moment(new Date(el.timestamp)).format("DD-MM-YYYY");
        let icon = "";
        let text = "";
        let message = el.message ? el.message : "";
        if (
          (filterVal === "Invite" || filterVal === "All") &&
          el.type &&
          el.type === "sendgrid"
        ) {
          icon =
            el.event === "Invite Sent"
              ? SentIcon
              : el.event === "delivered" || el.event === "open"
              ? DeliveryIcon
              : el.event === "click"
              ? ClickIcon
              : "";
          text =
            el.event === "Invite Sent"
              ? "Invitation Sent"
              : el.event === "delivered"
              ? "Invitation Delivered"
              : el.event === "open"
              ? "Invitation Opened"
              : el.event === "click"
              ? "Interview link clicked"
              : "";
        }
        if (
          (filterVal === "Sourced" || filterVal === "All") &&
          el.type &&
          el.type === "sourcing"
        ) {
          icon = FollowUp;
          text = el.status;
        }
        if (
          (filterVal === "Interview" || filterVal === "All") &&
          el.type &&
          el.type === "interview"
        ) {
          icon =
            el.event === "Logged In"
              ? LogIcon
              : el.event === "Interview Submitted"
              ? CompleteIcon
              : el.event.includes("Candidate")
              ? FollowUp
              : SubmitIcon;
          text = el.event;
        }

        if (
          (filterVal === "Conversation" || filterVal === "All") &&
          el.type &&
          (el.type === "sms" || el.type === "whatsapp" || el.type === "email")
        ) {
          icon = FollowUp;
          text = el.event;
        }
        if (filterVal === "Mail" && el.type && el.type === "email") {
          icon = FollowUp;
          text = el.event;
        }
        if (filterVal === "SMS" && el.type && el.type === "sms") {
          icon = FollowUp;
          text = el.event;
        }
        if (el.type === "whatsapp") {
          whatsappevent.push(el);
        }
        if (filterVal === "Whatsapp" && el.type && el.type === "whatsapp") {
          icon = FollowUp;
          text = el.event;
        }
        if (filterVal === "Debug" && el.type && el.type === "debug") {
          icon = el.metadata && el.metadata.Page ? CompleteIcon : "";
          text = el.metadata && el.metadata.Page;
        }
        if (
          filterVal === "Error" &&
          // || filterVal === "All"
          el.type &&
          el.type === "api"
        ) {
          icon =
            el.metadata && el.metadata.type === "error" ? CompleteIcon : "";
          text = el.metadata && el.metadata.type === "error" ? "Error" : "";
        }

        if (text && icon) {
          if (events.hasOwnProperty(date)) {
            const val = events[date];
            val.push({
              icon: icon,
              text: text,
              time:
                el &&
                el.timestamp &&
                new Date(el.timestamp).toLocaleTimeString(),
              message: message,
            });
            events[date] = val;
          } else {
            events[date] = [
              {
                icon: icon,
                text: text,
                time:
                  el &&
                  el.timestamp &&
                  new Date(el.timestamp).toLocaleTimeString(),
                message: message,
              },
            ];
          }
        }
      });

    this.setState({
      timeline: events,
      whatsappEvents: whatsappevent,
    });
  };

  handleSelect = (e) => {
    this.setState({ filter: e }, this.filterEvent);
  };

  sendWhatsapp = () => {
    this.props.ScheduleAction.createMessage({
      to: this.state.phone,
      medium: "whatsapp",
      body: this.state.currentMessage,
      inviteId: this.state.id,
      candidateId: this.state.candidateId,
    });
  };

  sendSMS = () => {
    this.props.ScheduleAction.createMessage({
      to: this.state.phone,
      medium: "sms",
      body: this.state.currentMessage,
      inviteId: this.state.id,
      candidateId: this.state.candidateId,
    });
  };

  sendEmail = () => {
    let email = "";
    this.state.email.map((item, index) => {
      if (index !== this.state.email.length - 1) {
        email += item + ",";
      } else {
        email += item;
      }
    });

    if (this.state.email.length === 1) {
      this.props.ScheduleAction.createEmail({
        to: email,
        subject: this.state.emailSubject,
        inviteId: this.state.id,
        candidateId: this.state.candidateId,
        body: this.state.currentMessage,
      });
    }
  };
  renderValues = (value, getTagProps) => {
    // optionsValue = value;
    let newVals = [];
    value &&
      value.map((val) => {
        let i = val.split(",");
        i.length === 1 ? newVals.push(i[0]) : i.map((el) => newVals.push(el));
      });

    if (newVals.length == 1) {
      return newVals.map((option, index) => (
        <Chip variant='outlined' label={option} {...getTagProps({ index })} />
      ));
    } else {
      return (
        <div style={{ display: "flex", alignItems: "center" }}>
          <Chip variant='outlined' label={newVals[0]} {...getTagProps(0)} />
          <div style={{ fontSize: "11px", marginLeft: "5px" }}>
            +{newVals.length - 1}
          </div>
        </div>
      );
    }
  };

  onChangeCurrentEmail = (value) => {
    // this.setState(
    //   {
    //     email: value,
    //   },
    //   () => {
    //     if (!value.length) {
    //       this.setState({
    //         emailPopup: false,
    //       });
    //     }
    //   }
    // );
  };

  sendViaEmail = () => {
    this.setState({
      emailPopup: true,
    });
  };

  sendViaWhatsapp = () => {
    this.setState({
      whatsappPopup: true,
    });
  };
  historyResponse = (id) => {
    this.props.history.push({
      pathname: `/response/single/${id}`,
    });
  };
  render() {
    const {
      data,
      count,
      scheduleCount,
      options,
      filter,
      timeline,
      email,
      phone,
      whatsappPopup,
      smsPopup,
      emailPopup,
      currentMessage,
      emailSubject,
    } = this.state;
    const candidateId = data && data.candidateId;
    const scheduleId = data && data.scheduleId;
    const jdId = data && data.jdId;
    const responseId = data && data.responseId;
    const inviteDate =
      scheduleId && scheduleId.createdAt && new Date(scheduleId.createdAt);
    const from =
      scheduleId &&
      scheduleId.fromTimestamp &&
      new Date(scheduleId.fromTimestamp);
    const to =
      scheduleId && scheduleId.toTimestamp && new Date(scheduleId.toTimestamp);

    // const timeline = {
    //   "09-08-2022": [
    //     { icon: GmailIcon, text: "Invitation Sent", time: "10:15" },
    //     { icon: GmailIcon, text: "Invitation Delivered", time: "10:15" },
    //   ],
    // };
    return (
      <div>
        <Navbar label={this.state.label} />
        <div className='HistoryPageMain'>
          <div style={{ display: "flex", justifyContent: "space-between" }}>
            <div className='BackButtonHeader'>
              <div
                className='arrow-div'
                // onClick={() => {
                //   this.props.history.push(`/invites/${this.state.scheduleId}`);
                // }}
                onClick={() => {
                  this.props.history.goBack();
                }}
              >
                <ArrowBackIcon style={{ color: "#21745D", width: "25px" }} />
              </div>
              <div className='score-details-heading'>Back</div>
            </div>
            <div className='TimelineFilter'>
              <SelectInput
                className='filter-timeline'
                onChange={this.handleSelect}
                options={options}
                value={filter}
                placeholder={filter && filter.label ? filter.label : "All"}
              />
            </div>
          </div>
          <div className='ContentArea'>
            <div className='InfoArea'>
              {data ? (
                <div>
                  <div className='TopInfo'>
                    <div className='NameSection'>
                      {candidateId && candidateId.name}
                    </div>
                    <div className='ActionSection'>
                      <img
                        src={GmailIcon}
                        alt=''
                        onClick={() =>
                          this.setState({
                            emailPopup: true,
                            smsPopup: false,
                            whatsappPopup: false,
                          })
                        }
                      />
                      <img
                        src={SmsIcon}
                        alt=''
                        onClick={() =>
                          this.setState({
                            smsPopup: true,
                            emailPopup: false,
                            whatsappPopup: false,
                          })
                        }
                      />
                      {data && data.whatsappEnabled && (
                        <img
                          src={WhatsappIcon}
                          alt=''
                          onClick={() =>
                            this.setState({
                              whatsappPopup: true,
                              smsPopup: false,
                              emailPopup: false,
                            })
                          }
                        />
                      )}
                    </div>
                  </div>
                  <div className='SubSection'>
                    <div style={{ display: "flex", alignItems: "center" }}>
                      {candidateId && candidateId.email}{" "}
                      {count > 1 ? (
                        <LightTooltip
                          title={`This email appeared across ${count} Job Titles and ${scheduleCount} schedules.`}
                          placement='top'
                        >
                          <div className='CountAppear'>{count}</div>
                        </LightTooltip>
                      ) : null}
                    </div>
                    <div style={{ textTransform: "capitalize" }}>
                      {data && data.status
                        ? data.status
                        : candidateId && candidateId.status
                        ? candidateId.status
                        : ""}
                    </div>
                  </div>
                  <div className='SubSection'>
                    <div>{candidateId && candidateId.phone}</div>
                    <div style={{ opacity: 0.58 }}>
                      {data && data.lastInteraction}
                    </div>
                  </div>
                  <div className='Details'>
                    <div className='DetailRow'>
                      <div className='Title'>Invite Date</div>
                      <div className='colon'>:</div>
                      <div className='value'>
                        {inviteDate
                          ? `${inviteDate.getDate()}-${inviteDate.toLocaleString(
                              "default",
                              { month: "short" }
                            )}-${inviteDate.getFullYear()}`
                          : "NA"}
                      </div>
                    </div>
                    <div className='DetailRow'>
                      <div className='Title'>Job Title</div>
                      <div className='colon'>:</div>
                      <div className='value'>
                        {jdId && jdId.title} {jdId && jdId.minExp}
                        {jdId && jdId.minExp > 1 ? " years" : " year"} to{" "}
                        {jdId && jdId.maxExp}
                        {jdId && jdId.maxExp > 1 ? " years" : " year"}
                      </div>
                    </div>
                    <div className='DetailRow'>
                      <div className='Title'>Assessment</div>
                      <div className='colon'>:</div>
                      <div className='value'>
                        {scheduleId &&
                        scheduleId.assessmentId &&
                        scheduleId.assessmentId.name
                          ? scheduleId.assessmentId.name
                          : "NA"}
                      </div>
                    </div>
                    <div className='DetailRow'>
                      <div className='Title'>Start Date</div>
                      <div className='colon'>:</div>
                      <div className='value'>
                        {from
                          ? `${from.getDate()}-${from.toLocaleString(
                              "default",
                              {
                                month: "short",
                              }
                            )}-${from.getFullYear()}`
                          : "NA"}
                      </div>
                    </div>
                    <div className='DetailRow'>
                      <div className='Title'>End Date</div>
                      <div className='colon'>:</div>
                      <div className='value'>
                        {to
                          ? `${to.getDate()}-${to.toLocaleString("default", {
                              month: "short",
                            })}-${to.getFullYear()}`
                          : "NA"}
                      </div>
                    </div>
                    <div className='DetailRow'>
                      <div className='Title'>Allowed Device</div>
                      <div className='colon'>:</div>
                      <div className='value'>
                        {scheduleId && scheduleId.modeOfTest === "LAPTOP"
                          ? "PC"
                          : scheduleId && scheduleId.modeOfTest === "MOBILE"
                          ? "Mobile"
                          : scheduleId && scheduleId.modeOfTest === "ALL_DEVICE"
                          ? "Both"
                          : "NA"}
                      </div>
                    </div>
                    <div className='DetailRow'>
                      <div className='Title'>User Device</div>
                      <div className='colon'>:</div>
                      <div className='value'>
                        {data && data.userAgent ? data.userAgent : "NA"}
                      </div>
                    </div>
                    {data && data.responseId ? (
                      <div className='DetailRow'>
                        <div className='Title'>Response</div>
                        <div className='colon'>:</div>
                        <div
                          className='value'
                          style={{ display: "flex", alignItems: "flex-end" }}
                        >
                          <div
                            onClick={() =>
                              this.historyResponse(data && data.responseId)
                            }
                          >
                            Click here
                          </div>

                          <HistoryIcon
                            onClick={(event) => {
                              if (event.ctrlKey) {
                                window.open(
                                  window.location.origin +
                                    window.location.pathname.replace(
                                      `history`,
                                      `response/single/${
                                        data && data.responseId
                                      }`
                                    )
                                );
                              } else {
                                this.historyResponse(data && data.responseId);
                              }
                            }}
                            style={{
                              width: "18px",
                              marginLeft: "10px",
                              cursor: "pointer",
                              color: "#04724d",
                            }}
                          />
                        </div>
                      </div>
                    ) : (
                      ""
                    )}
                    {data && data.rescheduleDateFrom ? (
                      <div className='DetailRow'>
                        <div className='Title'>Reschedule From</div>
                        <div className='colon'>:</div>
                        <div className='value'>
                          {`${new Date(
                            data.rescheduleDateFrom
                          ).getDate()}-${new Date(
                            data.rescheduleDateFrom
                          ).toLocaleString("default", {
                            month: "short",
                          })}-${new Date(
                            data.rescheduleDateFrom
                          ).getFullYear()}`}
                        </div>
                      </div>
                    ) : (
                      ""
                    )}
                    {data && data.rescheduleDateTo ? (
                      <div className='DetailRow'>
                        <div className='Title'>Reschedule To</div>
                        <div className='colon'>:</div>
                        <div className='value'>
                          {`${new Date(
                            data.rescheduleDateTo
                          ).getDate()}-${new Date(
                            data.rescheduleDateTo
                          ).toLocaleString("default", {
                            month: "short",
                          })}-${new Date(data.rescheduleDateTo).getFullYear()}`}
                        </div>
                      </div>
                    ) : (
                      ""
                    )}
                    {data && data.rescheduleCount ? (
                      <div className='DetailRow'>
                        <div className='Title'>Reschedule Count</div>
                        <div className='colon'>:</div>
                        <div className='value'>
                          {data && data.rescheduleCount}
                        </div>
                      </div>
                    ) : (
                      ""
                    )}
                  </div>
                </div>
              ) : null}
            </div>
            <div className='Timeline'>
              {Object.entries(timeline).length > 0 ? (
                Object.entries(timeline).map(([key, val], index) => {
                  return (
                    <div className='BubbleMain'>
                      <div className='DateDiv'>
                        {key}
                        <span></span>
                      </div>
                      <div className='Count' style={{ position: "relative" }}>
                        <div className='number'>{val && val.length}</div>
                        {index < Object.entries(timeline).length - 1 ? (
                          <div
                            className='line'
                            style={{
                              height: `${
                                val.length * 22 + (val.length + 1) * 15 + 2
                              }px`,
                              position: "absolute",
                              bottom: `-${
                                val.length * 22 + (val.length + 1) * 15 + 5
                              }px`,
                            }}
                          ></div>
                        ) : null}
                      </div>
                      <div className='BubleContent'>
                        <span className='bubbleContentSpan'></span>
                        {val && val.length > 0
                          ? val.map((el) => (
                              <div className='contents'>
                                <div className='text'>
                                  <img src={el && el.icon} alt='' />
                                  {el && el.text}
                                  {el.message ? (
                                    <LightTooltip
                                      title={el.message}
                                      placement='top'
                                    >
                                      <span className='infoSpan'>
                                        <span className='infoIcon'>i</span>
                                      </span>
                                    </LightTooltip>
                                  ) : null}
                                </div>
                                <div className='time'>{el && el.time}</div>
                              </div>
                            ))
                          : null}
                      </div>
                    </div>
                  );
                })
              ) : (
                <div className='TimelineNOData'>
                  Time line data not available
                </div>
              )}
            </div>
          </div>
          {/* {whatsappPopup ? (
            <MessagePopup
              color='#fff'
              background='#04724d'
              currentPhone={phone}
              closePopUpHandler={() => this.setState({ whatsappPopup: false })}
              currentMessage={currentMessage}
              currentMessageHandler={(e) =>
                this.setState({ currentMessage: e.target.value })
              }
              sendWhatsapp={this.sendWhatsapp}
            />
          ) : null} */}

          {emailPopup ? (
            <EmailPopup
              popupCloseHandler={() => this.setState({ emailPopup: false })}
              currentEmail={email}
              currentMessage={currentMessage}
              onChangeCurrentEmail={this.onChangeCurrentEmail}
              onChangeMessage={(e) => this.setState({ currentMessage: e })}
              renderValues={this.renderValues}
              emailSubject={emailSubject}
              subjectChangeHandler={(e) =>
                this.setState({ emailSubject: e.target.value })
              }
              sendEmail={this.sendEmail}
            />
          ) : null}

          {smsPopup ? (
            <MessagePopup
              background='#fff'
              color='#04724d'
              currentPhone={phone}
              closePopUpHandler={() => this.setState({ smsPopup: false })}
              currentMessage={currentMessage}
              currentMessageHandler={(e) =>
                this.setState({ currentMessage: e.target.value })
              }
              sendWhatsapp={this.sendSMS}
              sms={true}
            />
          ) : null}
        </div>
        {this.state.whatsappPopup ? (
          <Modal
            open={this.state.whatsappPopup}
            onClose={() => this.setState({ whatsappPopup: false })}
          >
            <Card className='WhatsAppModel'>
              <CardContent className='AreaComment'>
                <div className='headerComment'>
                  <div className='harderArea'>
                    <img src={WhatsappIcon} alt='' />
                    <div>Message To {phone}</div>
                  </div>
                  <img
                    src={CloseCommentIcon}
                    alt='close'
                    onClick={() => this.setState({ whatsappPopup: false })}
                  />
                </div>
                <div className='OldCommentSection'>
                  {this.state.whatsappEvents.map((item) => {
                    let today = new Date(item.timestamp);
                    let date =
                      today.getFullYear() +
                      "-" +
                      (today.getMonth() + 1) +
                      "-" +
                      today.getDate();
                    let time =
                      today.getHours() +
                      ":" +
                      today.getMinutes() +
                      ":" +
                      today.getSeconds();
                    return item.event == "whatsapp sent" ? (
                      <div className='oldcommentText right'>
                        <div className='timeComment'>
                          {date} {time}
                        </div>
                        <div className='noteComment'>{item.message}</div>
                      </div>
                    ) : (
                      <div className='oldcommentText left'>
                        <div className='timeComment'>
                          {date} {time}
                        </div>
                        <div className='noteComment'>{item.message}</div>
                      </div>
                    );
                  })}
                </div>
                <div className='NewCommentSection'>
                  {/* <div className="CommentText"> */}
                  <textarea
                    rows='3'
                    className='commentTextArea'
                    placeholder={
                      currentMessage ? currentMessage : "Enter Message"
                    }
                    onChange={(e) => {
                      this.setState({ currentMessage: e.target.value });
                    }}
                    value={currentMessage ? currentMessage : ""}
                  ></textarea>
                  {/* </div> */}
                  <div className='sendComment'>
                    <Button
                      backgroundColor='#04724D'
                      color='#ffffff'
                      width='100px'
                      height='34px'
                      fontWeight='bold'
                      className='sendButtonComment'
                      fontSize='11px'
                      disabled={!currentMessage}
                      onClick={this.sendWhatsapp}
                    >
                      Send
                      <SendIcon
                        style={{
                          fontSize: "medium",
                          color: "#fff",
                          marginLeft: "20px",
                        }}
                      />
                    </Button>
                  </div>
                </div>
              </CardContent>
            </Card>
          </Modal>
        ) : null}
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    scheduleReducer: state.ScheduleReducer,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    ScheduleAction: bindActionCreators(ScheduleActions, dispatch),
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(InviteHistory));
